.kk__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.kk__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    max-width: 1000px;

    text-align: left;
}

.kk__footer-links div {
    width: 250px;
    margin: 1rem;

}

.kk__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.kk__footer-links_logo img {
    width: 100%;

    margin-bottom: 1rem;
}

.kk__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
}

.kk__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.kk__footer-links_div h4 {
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: white;
    margin: 0.5rem;

}

.kk__footer-links_div p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;

    margin: 0.5rem;
}
.kk__footer-links_div a {
    cursor: pointer;
}

.kk__footer-copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
    margin-bottom: -2rem;
}
.kk__footer-copyright p {

    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
}

@media screen and (max-width: 550px) {
.kk__footer-links div{
    margin: 1rem 0;
}
}