.kk__contact {
    display: flex;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.kk__contact-contacts{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.kk__contact-contacts p{
    font-family: var(--font-family);
    color: white;

    margin: 0.5rem;
}



.kk__contact__form{
    flex: 2.5;
}

.kk__contact .kk__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.kk__contact__form-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.kk__contact__form-container-send{
    display: flex;
    justify-content: left;
    align-items: center;
}

.kk__contact__form input, textarea{
    min-height: 50px;
    font-style: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: white;
    margin: 1rem;

    border-radius: 5px;
}

.kk__contact__form button{
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    background: #FF4820;
    color: white;
    cursor: pointer;
    outline: none;
    border: none;
    padding: 0 1rem;

    border-radius: 5px;
}



@media screen and (max-width: 750px){
    .kk__contact {
        flex-direction: column;
    }
.kk__contact-contacts{
    margin-top: 0.5rem;
}
.kk__contact .kk__features-container__feature{
    margin: 1rem 0;
}
}
@media screen and (max-width: 550px){
    .kk__contact {
        padding: 1.5rem;
    }
.kk__contact-contacts{
    font-size: 14px;
}

}