.kk__brand{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: -4rem;
}

.kk__brand div{
    flex: 1;
    max-width: 100px;
    margin: 1rem;

    display:flex;
    justify-content: center;
    align-items: center;
}

.kk__brand div img{
    max-width: 50px;
}

@media screen and (max-width:500px){
    .kk__brand div img{
        max-width: 35px;
    }
    
}