.kk__header {
    display: flex;
    flex-direction: row;


}

.kk__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.kk__header-content h1 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.kk__header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;

    color: var(--color-text);

    margin-top: 1.5rem;
}

.kk__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
    flex-direction: row;


}

.kk__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-style: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: white;

    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.kk__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    background: #FF4820;
    color: white;
    cursor: pointer;
    outline: none;
    border: none;
    padding: 0 1rem;

    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

}


.kk__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-top: 2rem;
}

.kk__header-content__people img {
    width: 180px;
}

.kk__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-style: normal;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;

}

.kk__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}

.kk__header-image img {
    width: 100%;
}

@media screen and (max-width: 1050px) {

    .kk__header {
        flex-direction: column;

    }

    .kk__header-content {
        margin: 0 0 3rem;
    }


}

@media screen and (max-width: 850px) {

    .kk__header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .kk__header p {
        font-size: 16px;
        line-height: 24px;
    }

    .kk__header-content__people {
        flex-direction: column;
    }

    .kk__header-content__people p {
        margin: 0;
    }

    .kk__header-content__input input,
    .kk__header-content__input button {
        font-size: 16px;
        line-height: 24px
    }


}


@media screen and (max-width: 490px) {
    .kk__header{
        margin-top: -2rem;
    }
    .kk__header h1 {
        font-size: 36px;
        line-height: 48px;

    }

    .kk__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .kk__header-content__input input,
    .kk__header-content__input button {
        font-size: 12px;
        line-height: 16px
    }

}