.kk__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100px;
}

.kk__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.kk__navbar-logo {
    margin-left: 2rem;
}

.kk__navbar-links_logo img {
    width: 200px;
}

.kk__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.kk__navbar-contact {
    display: flex;
    justify-content: flex-end;
    align-items: center;

}

.kk__navbar-links_container p,
.kk__navbar-contact p,
.kk__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;

    margin: 0 1rem;
    cursor: pointer;
}

.kk__navbar-contact a,
.kk__navbar-container-contact a {
    padding: 0.5rem 1rem;
    cursor: pointer;

    background: #FF4820;
    border-radius: 5px;
}


.kk__navbar-menu {
    margin-left: 1rem;
    padding: 2rem;

    display: none;
    position: relative;
}

.kk__navbar-menu svg {
    cursor: pointer;
}

.kk__navbar-menu_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    text-align: end;
    width: 100vw;
    background: var(--color-footer);
    padding: 0;
    position: absolute;
    top:75px;
    right: -100px;
    margin: 0rem;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.kk__navbar-menu_container p {
    margin: 1rem 0;
    text-align: center;
}

.kk__navbar-container-contact {
    display: none;
}
.kk__divider-solid {
    border-top: 1px solid rgba(255, 255, 255, 0.25);
    display: block;
}

@media screen and (max-width: 1050px) {
    .kk__navbar-links_container {
        display: none;
    }

    .kk__navbar-menu {
        display: flex;
    }
    .kk__navbar-menu_container p{
        padding: 0.3rem;
    }
    .kk__divider-solid {
        width: 100vw;
    }
    .last-child{
        display: none;
    }

}

@media screen and (max-width: 850px) {
    .kk__navbar_logo img {
        padding-left: 20px;
    }


    .kk__navbar-container-contact{
        padding: 0.5rem;
    }

 
}

@media screen and (max-width: 550px) {

    .kk__navbar-menu{
        padding: 20px;
    }

    .kk__navbar_logo img {
        width: 80%;
    }

    .kk__navbar-contact {
        display: none;
    }

    .kk__navbar-container-contact,.last-child {
        display: block;
    }
}